import {
  Box,
  Button,
  Container,
  TextField,
  Typography,
  useMediaQuery,
} from "@mui/material";
import { makeStyles, useTheme } from "@mui/styles";
import { useFormik } from "formik";
import React, { useLayoutEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Preloader from "../../components/Preloader";
import { getParcelDetails } from "../../redux/reducers/ordering";
import CommonHero from "../../shared/CommonHero";
import Results from "./Results";

const Tracking = ({ setLoadingParcel, loadingParcel }) => {
  const classes = useStyles();
  const theme = useTheme();
  const md = useMediaQuery(theme.breakpoints.down("md"));
  const dispatch = useDispatch();

  const { details } = useSelector(({ ordering }) => ordering);

  const { detailsID } = useSelector(({ ordering }) => ordering);
  const [submit, setSubmit] = useState(false);

  const formik = useFormik({
    initialValues: {
      id: detailsID ? detailsID : "",
    },
    onSubmit: (values) => {
      //show preloader
      setLoadingParcel(true);
      setSubmit(true);
      // get details
      dispatch(getParcelDetails(values.id));
    },
  });

  const firstUpdate = useRef(true);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    // hide preloader
    setLoadingParcel(false);
  }, [details]);

  return (
    <>
      {loadingParcel && <Preloader />}
      <CommonHero title="Отследить заказ" />
      <section className={classes.section}>
        <Container maxWidth="xl">
          <form className={classes.form} onSubmit={formik.handleSubmit}>
            <Typography varian="body2" sx={{ fontWeight: "normal" }} mb={3}>
              Достаточно ввести номер заказа или экспресс-накладной в поле для
              отслеживания и нажать кнопку «Отследить».
            </Typography>
            <Box
              display="flex"
              wrap="nowrap"
              sx={{
                flexDirection: { xs: "column", md: "row" },
              }}
            >
              <TextField
                name="id"
                onChange={formik.handleChange}
                value={formik.values.id}
                variant="outlined"
                required
                fullWidth
                placeholder="Введите номер заказа"
              />
              <Button
                variant="contained"
                size="large"
                type="submit"
                color="primary"
                style={{ marginLeft: md ? 0 : 16, marginTop: md ? 16 : 0 }}
              >
                Отследить
              </Button>
            </Box>
          </form>
          {!details?.id && submit ? (
            <Typography variant="h4" mt={4}>
              По вашему запросу ничего не найдено :)
            </Typography>
          ) : (
            details?.id && <Results details={details} />
          )}
        </Container>
      </section>
    </>
  );
};

export default Tracking;

const useStyles = makeStyles(() => ({
  section: {
    padding: "60px 0 120px",
    background: "#F6F7F9",
  },

  form: {
    maxWidth: 567,
  },
}));
